const state = {
  purseVersion: '0.9.9',
  addressListItems: [],
  accountTxList: [],
};

const getters = {
  getPurseVersiont: state => state.purseVersion,
  getAddressList: state => state.addressListItems,
};

const mutations = {
  setAddressList (state, data) {
    state.addressListItems = data
  },
};

const actions = {};

export default {
  state,
  getters,
  mutations,
  actions
}



